import React from 'react';
import ReactDOM from 'react-dom';

import CanvasDraw from "react-canvas-draw";

import { BlockPicker } from 'react-color';

import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import 'bootstrap/dist/css/bootstrap.min.css';


import './index.css';

class StickerBoard extends React.Component {
    constructor(props) {
      super(props);
      this.state = {file: '',
      imagePreviewUrl: '', 
      stickers: Array(),
      graff: null
    };
    }
  
    _handleSubmit(e) {
      e.preventDefault();
      let url;
      if (!this.state.graff){
        url = this.state.imagePreviewUrl;
        this._renderSticker(this.state.file);
      } else {       
        url = localStorage.getItem('graff');
       
        this._renderSticker(url);
        this.setState({
          file: localStorage.getItem('graff'),
          graff: null
        })
      }
      

     
      let stickerz = this.state.stickers.concat(url);
      this.setState({
        stickers: stickerz
      });

      this._clearPreview(e);
          
      
    }
  
    _handleImageChange(e) {
      e.preventDefault();
  
      let reader = new FileReader();
      let file = e.target.files[0];
  
      reader.onloadend = () => {
        this.setState({
          file: file,
          imagePreviewUrl: reader.result,
          graff: false
        });
      }
  
      reader.readAsDataURL(file);

    }

    _clearPreview(e) {
      e.preventDefault();
      this.setState({
        file: '',
        imagePreviewUrl: ''
      });


    }

    _renderSticker(url) {
      
      return(
        <Sticker url={url}/>       
      )
      
    }
  
    render() {
      let {imagePreviewUrl} = this.state;
      let $imagePreview = null;
      let $fileControls = null;
      let $postButton = null;
      
      if (this.state.graff === null){
        $postButton = null;
      } else if (this.state.graff === true){
        $imagePreview = (<div className="previewText"><Drawer/></div>);
        $postButton = (
          <button className="submitButton controls_cust pVr75 _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ" 
              type="submit" 
              onClick={(e)=>this._handleSubmit(e)}>POST!</button>
        );
      } else {
        $fileControls = (
          <div>
          <input className="hidden controls_cust pVr75 _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ" 
          title="Choose file"
          type="file" 
          id='file-upload'
          onChange={(e)=>this._handleImageChange(e)}               
        />
        <label 
          className="controls_cust _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ" 
          for="file-upload">
          File
        </label>
        <button className="controls_cust _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ"

              onClick={(e) => {
                this._clearPreview(e)
              }}>
              ✖ Clear
        </button> 
        </div>
        )
      }

      if (imagePreviewUrl) {
        $imagePreview = (<img src={imagePreviewUrl} alt="" />);
        $postButton = (
          <button className="submitButton controls_cust pVr75 _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ" 
              type="submit" 
              onClick={(e)=>this._handleSubmit(e)}>POST!</button>
        );
      } else {
        
      }

    const sticks = this.state.stickers;
      
    let stickers = sticks.map((url) =>
      <div>{this._renderSticker(url)}</div>
    );       
  
      return (        
        <div className="previewComponent">

          <div>
            <DropdownButton variant="cust" id="dropdown-item-button" title="Add Sticker">              
              <Dropdown.Item variant="cust" as="button"
                onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                          graff: false,
                          imagePreviewUrl: ''
                          })
                      }}
              >Image</Dropdown.Item>
              <Dropdown.Item variant="cust" as="button"
                        onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                          graff: true,
                          imagePreviewUrl: ''
                          })
                      }}>Drawing</Dropdown.Item>              
            </DropdownButton>
          </div>
          <form onSubmit={(e)=>this._handleSubmit(e)}>
            <div className="draw_controls">
                {$fileControls}

            </div>
          
          <div className="imgPreview">
            
            {$imagePreview}
            {$postButton}

          </div>
         
          </form>

        <div>        
          
        </div>
        <div>
          {stickers}               
        </div>


          
        </div>
        
      )
    }
  }

  class Sticker extends React.Component {
    constructor(props) {
      super(props);      

    }
      render() {
        return (
        <div>
          <div>   
          <div class="user_title">
          <div class="_2FCQQ">
            <img src="https://www.ultimate-guitar.com/static/users/4/1/5/8523514.gif?d=d&amp;h=1598365467" class="_377Nx _1dpi8 _1Ln-H" alt="andie.ape"></img>
          </div>
          <div class="user_name">
            <a class="_2KJtL _1ofov kWOod">user.name</a>
          </div>
          </div>  
          <div class="sticker_item">
           <img src={this.props.url}/>
          </div>   
          
          </div>
          
        </div>
        )
      }
    }
  

  class Drawer extends React.Component {
    constructor(props){
      super(props);
      this.parent = React.createRef()
      this.state = {

        value: 12,        
        brushRadius: 12,
        brushColor: "#444",
        width: 400,
        height: 200,
        showPicker: false   

      }
    }

    _handleColorChange = (color, event) => {
     
      this.setState({ brushColor: color.hex });
     
    };

    render(){
      
      let $colorPicker = null;

      if (!this.state.showPicker){
        $colorPicker = ("");
      } else {
        $colorPicker = (<BlockPicker 
          className="picker-block"
          triangle="hide" 
          color={this.state.brushColor}
          onChangeComplete={ this._handleColorChange }
        />);
      }

      return (
        <div ref={this.parent}>
          <div className="draw_controls">
            <div>
             
              <div className="color-btn-wrap controls_cust pVr75 _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ"
              
                onClick={() => {
                    this.setState({
                      showPicker: !this.state.showPicker
                    });                  
                  }}

              >
               <div style={{
                  backgroundColor: this.state.brushColor}} 
                  className='color-btn'               
                  >               
                </div>
                <div>Color</div>
                          
              
              </div>
              
              {$colorPicker}
            </div>
            <div>
              <div className="controls_cust pVr75 _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ"

                  onClick={() => {
                    this.canvasElem.clear();
                    localStorage.setItem('graff', this.canvasElem.canvas.drawing.toDataURL());
                  }}>
                ✖ Clear
              </div>
              <div  className=" controls_cust pVr75 _27k0J _2PAY1 _3EpOX _3K_s7 YJMQR _2pMPQ _1G2cJ"           
                  onClick={() => {
                    this.canvasElem.undo();
                    localStorage.setItem('graff', this.canvasElem.canvas.drawing.toDataURL());
                  }}>
                ⎌ Undo
              </div>
            </div> 
            <div class="brush div">
              <input
              class="brush input"
              type="range"
              id="brush_size" name="brush"
              min="1" 
              max="30"
              value={this.state.brushRadius}
              step="1"
              onChange={(e) => {    
                this.setState({
                  brushRadius: e.target.value
                })
              }}
              />
              <div class="brush label">
                <label  for="brush">Brush size</label>
              </div>
              
            </div>

          </div>
          <div>
            <div class='draw_pad'>
                <CanvasDraw 
                  onChange={(e) => {    
                    localStorage.setItem('graff', this.canvasElem.canvas.drawing.toDataURL())
                   }}
                  canvasWidth={this.state.width}
                  canvasHeight={this.state.height}
                  brushRadius={this.state.brushRadius}
                  ref={canvasDraw => (this.canvasElem = canvasDraw)}
                  hideGrid
                  brushColor = {this.state.brushColor}

                />
            </div>
          </div>
        </div>
               
           
        
        
        
            
           
       
        
        
      )
    }
  }

  

  ReactDOM.render(
    <StickerBoard />,    
    document.getElementById('root')
  );
